import React, { Component } from 'react';
import { Button  ,WingBlank, Toast ,Modal,InputItem} from 'antd-mobile';
import Nav from '../header/header';
import style from '../App.css';
import Input from 'antd-mobile/lib/input-item/Input';

const prompt = Modal.prompt;
function closest(el, selector) {
    const matchesSelector = el.matches || el.webkitMatchesSelector || el.mozMatchesSelector || el.msMatchesSelector;
    while (el) {
        if (matchesSelector.call(el, selector)) {
        return el;
        }
        el = el.parentElement;
    }
    return null;
}
class SignIn extends Component{
    constructor(props){
        super(props)
        this.state = {
            codeMsg:"",
            maxtime:59,
            codeClick:true,
            modal1: false,
            inputValue:''
        }

    }
    countDown(){    
        console.log('count')
        var msg = ""
        this.timer = setInterval(()=>{
            const {maxtime}=this.state;
            if(maxtime >=1){
                var seconds = Math.floor(maxtime % 60);
                msg = ""+seconds ;
                this.setState({ maxtime: maxtime - 1 ,codeMsg:msg});
            }else{
                msg = "";
                this.setState({ codeMsg:msg, codeClick:true});
                clearInterval(this.timer);
            }
        },1000)
    }
    showModal = key => (e) => {
        e.preventDefault(); //  Android 
        this.setState({
            [key]: true,
        });
        if(this.state.codeClick){
            this.countDown();
        };
    }
    onClose = key => () => {
    this.setState({
        [key]: false,
    });
    }
    onWrapTouchStart = (e) => {
        // fix touch to scroll background page on iOS
        if (!/iPhone|iPod|iPad/i.test(navigator.userAgent)) {
            return;
        }
        const pNode = closest(e.target, '.am-modal-content');
        if (!pNode) {
            e.preventDefault();
        }
    }
    render() {
        const header = ''
        console.log(this.state.codeMsg)
        const {inputValue}  = this.state;
        // const { getFieldProps } = this.props.form;
        return (
            <div>
            <div className={style['fenqi-fixed']}>
                <Nav {...this.props} header={header}></Nav>
            </div>
            <div className={style['signIn-content']}>
                <h3 style={{textAlign:'center'}}></h3>
                <p>
                                          ,                                                                                              
                </p>
                <p>
                                                                                                                                         
                </p>
                <p>
                     2013   7  15                               
                </p>
                <p></p>
                <p>                                                                     </p>
                <p></p>
                <p></p>
                <p>1        </p>
                <p>2        </p>
                <p>3</p>
                <p></p>
                <p>1</p>
                <p>236</p>
                <p>3201911720211217201911720211217</p>
                <p>4        12/</p>
                <p></p>
                <p>112        24%/</p>
                <p>2</p>
                <p></p>
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <p>4</p>
                <p></p>
                <p>1</p>
                <p>2</p>
                <p>3</p>
                <p>4</p>
                <p></p>
                <p>1</p>
                <p>2</p>
                <p>3200541</p>
                <p>4</p>
                <p>                                         </p>
                <p> 20181217                               20181217</p>
            </div>
                <div className={style['fixed-bottom']}>
                    <WingBlank>
                    <Button type="primary" onClick={this.showModal('modal1')}></Button>
        {/* <WhiteSpace /> */}
        <Modal
          visible={this.state.modal1}
          transparent
          maskClosable={false}
          onClose={this.onClose('modal1')}
          title=""
          footer={[{ text: 'Ok', onPress: () => { 
            setTimeout(() => {
                        if(inputValue !== ''){
                            if(inputValue === '8888'){
                            this.onClose('modal1')();
                            this.props.history.push('/success')
                            }else{
                            Toast.info('', 1);
                        }
                    }else{
                        Toast.info('', 1);
                    }
                    }, 1000);  
             } }]}
          wrapProps={{ onTouchStart: this.onWrapTouchStart }}
          afterClose={() => { alert('afterClose'); }}
        >
          <div style={{ height: 100, overflow: 'scroll' }}>
            {this.state.codeMsg}
            <div>
                <div className="sign-input">
                    <label>
                        <InputItem 
                        onChange={(value)=>{
                            this.setState({
                                inputValue:value
                            })
                        }} 
                        value={inputValue}
                        maxLength={4}
                        ></InputItem>
                    </label>
                </div>
            </div>
          </div>
        </Modal>
                        {/* <Button type="primary" onClick={()=>{this.showModal('modal1')}}>
                            
                        </Button>
                        <Modal
                            visible={this.state.modal1}
                            // transparent
                            // maskClosable={false}
                            // onClose={this.onClose('modal1')}
                            title=''
                        //     footer={[{ text: 'Ok', onPress: () => {setTimeout(() => {
                        //         if(inputValue !== ''){
                        //             if(inputValue === '8888'){
                        //             this.props.history.push('/success')
                        //             }else{
                        //             Toast.info('', 1);
                        //         }
                        //     }else{
                        //         Toast.info('', 1);
                        //     }
                        //     }, 1000);
                        // } }]}
                            // wrapProps={{ onTouchStart: this.onWrapTouchStart }}
                            // afterClose={() => { alert('afterClose'); }}
                            >
                            <div style={{ height: 100, overflow: 'scroll' }}>
                                scoll content...<br />
                                scoll content...<br />
                                scoll content...<br />
                                scoll content...<br />
                                `${this.state.codeMsg}`
                                <Input value={inputValue} />
                            </div>
                        </Modal> */}
                        {/* <Button type="primary" onClick={() => {
                            if(this.state.codeClick){
                                this.countDown();
                            };
                            prompt(
                        '',
                        React.createElement('h1', {id: 'recipe', 'data-type': 'title'}, `${this.state.codeMsg}`),
                        
                        [
                            { text: '' },
                            {
                                text: '',
                                
                                onPress: value => new Promise((resolve, reject) => {
                                    setTimeout(() => {
                                        if(value !== ''){
                                            if(value === '8888'){
                                            resolve();
                                            this.props.history.push('/success')
                                            }else{
                                            reject();
                                            Toast.info('', 1);
                                        }
                                    }else{
                                        reject();
                                        Toast.info('', 1);
                                    }
                                    }, 1000);
                                }),
                              },

                        ],
                        'default', null,
                        )}}></Button> */}
                    </WingBlank>
                </div>
            </div>
        );
    }
}

export default SignIn;


