import React, { Component } from 'react'
import { Button , Flex,InputItem ,WingBlank, WhiteSpace } from 'antd-mobile';
import Nav from '../header/header';
import ImagePickerExample from '../imagePicker/imagePicker';
import style from '../App.css';
class About extends Component{
    submit=()=>{
        this.props.history.push('/plan');
    }
    render() {
    const header = ""
    return (
        <div>
            <Nav  {...this.props} header={header}></Nav>
            <p className={style['cardIdTitle']}></p>
            <Flex>
                <Flex.Item className={style['cardId']}>
                    <ImagePickerExample/>
                    <span></span>
                </Flex.Item>
                <Flex.Item className={style['backCardId']}>
                    <ImagePickerExample/>
                    <span></span>
                </Flex.Item>
            </Flex>
            <WhiteSpace/>
            <WingBlank  size="md">
                    <InputItem
                        type='text'
                        placeholder=""
                        clear
                        // moneyKeyboardAlign="left"
                        // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                    >
                    </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder=""
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder=""
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder="1994410"
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder=""
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder="627123199404101234"
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                <InputItem
                    type='text'
                    placeholder="2017-12-12"
                    clear
                    // moneyKeyboardAlign="left"
                    // moneyKeyboardWrapProps={moneyKeyboardWrapProps}
                >
                </InputItem>
                </WingBlank>
                <WhiteSpace size="lg" />
                <WingBlank  size="md">
                    <Button type="primary" onClick={this.submit}></Button>
                </WingBlank>
        </div>
    )
        
  }
}
export default About;


